import "core-js/modules/es.array.push.js";
import articleApi from '../../model/article';
export default {
  data() {
    return {
      products: [],
      allProducts: [],
      // 存储所有产品的原始列表
      orders: [],
      // 用于存储查询到的所有订单（若有多个）
      order: null,
      // 用于存储当前查询到的单个订单
      selectProductName: '',
      queryinfo: '',
      // 商品搜索条件
      orderQuery: '',
      // 订单号查询条件
      imageDialogVisible: false,
      // 控制图片弹窗显示
      currentImage: '',
      // 当前显示的图片URL
      imageTitle: '',
      // 图片标题
      activeTab: 'all' // 当前激活的标签页
    };
  },

  created() {
    this.getProductList();
  },
  methods: {
    // 显示社交媒体大图
    showSocialImage(imageUrl, title) {
      this.currentImage = imageUrl;
      this.imageTitle = title;
      this.imageDialogVisible = true;
    },
    async getProductList() {
      try {
        if (this.queryinfo === '') {
          this.allProducts = await articleApi.postProduct('all');
          this.products = [...this.allProducts]; // 默认显示所有产品
        } else {
          this.products = await articleApi.postProduct(this.queryinfo);
          this.allProducts = [...this.products]; // 搜索结果也保存到allProducts
        }
      } catch (error) {
        console.error('获取产品列表失败', error);
      }
    },
    // 处理标签页点击事件
    handleTabClick(tabName) {
      this.activeTab = tabName;
      if (tabName === 'all') {
        // 显示所有产品
        this.products = [...this.allProducts];
        return;
      }

      // 从所有产品中随机选择3个产品显示
      if (this.allProducts.length > 0) {
        // 复制原始产品列表，避免修改原始数据
        const productsCopy = [...this.allProducts];
        const randomProducts = [];

        // 确定要选择的产品数量，最多3个，但如果产品总数少于3个，则全部显示
        const selectCount = Math.min(3, productsCopy.length);
        for (let i = 0; i < selectCount; i++) {
          // 随机选择一个索引
          const randomIndex = Math.floor(Math.random() * productsCopy.length);
          // 将选中的产品添加到结果数组
          randomProducts.push(productsCopy[randomIndex]);
          // 从复制的数组中移除已选产品，避免重复选择
          productsCopy.splice(randomIndex, 1);
        }
        this.products = randomProducts;
      }
    },
    async showPay(id) {
      let order = await articleApi.postProductOrderNo(id);
      if (order.payment_no != '') {
        this.$refs['orderPay'].showName(this.selectProductName);
        this.$refs['orderPay'].show(order);
      } else {
        this.$message.error('获取订单号失败');
      }
    },
    handleProductClick(productId, productName) {
      this.selectProductName = productName;
      this.showPay(productId);
    },
    // 商品搜索按钮点击事件处理函数
    async handleSearch() {
      if (this.queryinfo === '') {
        this.allProducts = await articleApi.postProduct('all');
        this.products = [...this.allProducts];
      } else {
        this.products = await articleApi.postProduct(this.queryinfo);
        this.allProducts = [...this.products];
      }
      // 重置为"全部"标签
      this.activeTab = 'all';
    },
    async showPay() {
      this.$router.push('/server');
    },
    // 订单查询处理
    async handleOrderSearch() {
      if (!this.orderQuery) {
        this.order = null;
        this.orders = []; // 清空订单数组
        return;
      }

      // 模拟订单查询，按订单号进行查询
      try {
        const orderResponse = await articleApi.postOrderQuery(this.orderQuery);
        if (orderResponse && orderResponse.length > 0) {
          this.orders = orderResponse; // 存储所有查询到的订单
          this.order = orderResponse[0]; // 假设返回第一个订单作为当前订单
        } else {
          this.orders = []; // 若没有找到订单
          this.order = null;
        }
      } catch (error) {
        console.error('查询订单失败', error);
        this.orders = []; // 如果出错，清空订单数组
        this.order = null; // 清空当前订单
      }
    },

    // 点击介绍链接时处理的函数
    handleDetailClick(detailLink) {
      window.open(detailLink, '_blank');
    }
  },
  computed: {
    loggedIn() {
      return this.$store.state.loggedIn;
    }
  }
};